@import url(~bootstrap/dist/css/bootstrap.min.css);
@import url(~prism-themes/themes/prism-vs.css);
.container {
  padding-left: 20px;
  padding-right: 20px; }

.container-small {
  max-width: 800px; }

ul li, ol li {
  margin-bottom: 7px; }

.border-top {
  border-top: 1px solid #eaeaea !important; }

.border-bottom {
  border-bottom: 1px solid #eaeaea !important; }

.bg-light {
  background-color: #fafafa !important; }

.muted {
  color: #757575; }

.dark-link {
  font-weight: 500;
  color: #000000;
  text-decoration: none; }
  .dark-link:hover {
    color: #101010; }

.border-through {
  width: 100%;
  text-align: center;
  border-bottom: 2px solid #f5f5f5;
  line-height: 0.1em;
  margin: 10px 0 20px; }
  .border-through span {
    background: #fff;
    padding: 0 10px; }

code:not([class]) {
  background: #fafafa;
  border-radius: 6px;
  display: inline-block;
  font-size: inherit;
  padding: 0 5px;
  word-wrap: normal;
  box-shadow: none; }
